.tasks{
    width: 100%;
    height: 28vw;
    overflow: scroll;
}

.task{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid black;
}

.task_item_left{
    margin-left: 25px;
    display: flex;
    align-items: center;
}

.task_text{
    margin-left: 30px;
    font-size: 20px;
}

.task_item_right{
    margin-right: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dropdown{
    margin-right: 15px;
}