.input_task_container{
    height: 5vh;
    background-color: #f2f2f2;
    padding: 30px;
    display: flex;
    align-items: center;
}

.input_task_container input{
    display: block;
    width: 80%;
    height: 80%;
    margin: 0 auto;
    font-size: 28px;
}

.input_task_container button{
    max-width: 10%;
    height: 100%;
    margin: 0;
}