.footer{
    margin-top: auto;
    height: 23vh;
    width: 100%;
    background-color: #f2f2f2;
    display: flex;
    padding: 15px;
}

.footer_item{
    flex:1;
    text-align: center;
}

h3{
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
}

button{
    background-color: blue;
    color: #fff;
    width: 70%;
    height: 32px;
    border-radius: 5px;
    font-size: 20px;
    margin-bottom: 20px;
}

button:hover{
    cursor: pointer;
}

.chkb_container{
    width: 30%;
    display: flex;
    align-items: center;
    margin: 0 auto;
}

.chkb_container input {
    margin-right: 10px;
}

.chkb_container p{
    margin: 0;
    padding: 0;
    padding-left: 10px;
}



